



import { Component, Mixins, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
@Component({
    name: "MyAvatar"
})
export default class MyAvatar extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;
    @Prop()
    size!: number | string;
}
